import React, { useEffect, useState, useRef, useReducer } from "react";
import axios from "axios";
import styled, { keyframes } from "styled-components";
import CityComponent from "./modules/CityComponent";
import WeatherComponent from "./modules/WeatherInfoComponent";
import leftArrowIcon from "./assets/left-arrow.png";
import Classes from './App.module.css';

const ContainerBody = styled.div`
  font-family: "Open Sans", sans-serif;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(rgb(172, 154, 97), #eee);
`;

const animateContainer = keyframes`
    0%{
        opacity: 0;
        transform: translateY(-100%);
      }

    80%{
      transform: translateY(10%);
    }

    100%{
        opacity: 1;
        transform: translateY(0);
      }
`;

const Container = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  box-shadow: 0 3px 6px 0 #555;
  width: 25%;
  padding: 20px 10px;
  border-radius: 4px;
  background: white;
  font-family: "Montserrat";
  animation: ${animateContainer} 500ms linear;
      @media(max-width: 1000px){
        width: 50%;
      }
`;

const AppLabel = styled.span`
  color: black;
  font-size: 18px;
  font-weight: bold;
`;

const LeftArrow = styled.div`
  width: 20px;
  height: 20px;
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
`;

const APP_KEY = "2d81fef83b338fbb183f884a19152898";

const initialState = {
  searchWeatherState: true,
};

function reducer(state = initialState, options) {
  switch (options.type) {
    case "SEARCH_WEATHER_TRUE":
      return {
        searchWeatherState: true,
      };
    case "SEARCH_WEATHER_FALSE":
      return {
        searchWeatherState: false,
      };
    default:
      return state;
  }
}

function App() {
  const [city, updateCity] = useState();
  const [weather, updateWeather] = useState();
  const [{ searchWeatherState }, dispatch] = useReducer(reducer, initialState);

  const inputElementRef = useRef();

  useEffect(() => {
    async function fetch() {
      if (city) {
        const response = await axios.get(
          `https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=${APP_KEY}`
        );

        //Update weather state when fetched from API
        updateWeather(response.data);
      }
    }
    fetch();
  }, [city]);

  const onSubmitHandler = async (event) => {
    event.preventDefault();
    let enteredCity = inputElementRef.current.value;
    //update back button state to False
    dispatch({ type: "SEARCH_WEATHER_FALSE" });
    //Update CITY state when click search button
    updateCity(enteredCity);
  };

  const backTOSearchWeather = () => {
    //update back button state to False
    dispatch({ type: "SEARCH_WEATHER_TRUE" });
    //reset weather details when user click back button
    updateWeather();
  };

  return (
    <ContainerBody>
      <div className={Classes.box}>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
      </div>
      <Container>
        <AppLabel>React Weather App</AppLabel>
        {weather && !searchWeatherState && (
          <LeftArrow onClick={backTOSearchWeather}>
            <img src={leftArrowIcon} alt="back" />
          </LeftArrow>
        )}
        {weather && !searchWeatherState ? (
          <WeatherComponent weatherInfo={weather} />
        ) : (
          <CityComponent
            ref={inputElementRef}
            onClickSearch={onSubmitHandler}
          />
        )}
      </Container>
    </ContainerBody>
  );
}

export default App;
